import axios from "axios";
import React,{ useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./sidebar.css";

export default function Sidebar() {
  const [cats, setCats] = useState([]);//to fetch categories 

  useEffect(() => {
    // post man yerindeki her sey burada
    const getCats = async () => {
      const res = await axios.get("/categories");
      setCats(res.data);
    };
    getCats();
  }, []);
  return (
    <div className="sidebar">
      <div className="sidebarItem">
        <span className="sidebarTitle">ABOUT ME</span>
        <img width={200}
          src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c897ee06-eed4-4abb-b875-a30b63e061ea/ddc60af-5b680d72-6dd4-4c28-bb8c-6f064c703472.png?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..-SWnihbBbIl7TDw4ai4rbVqqhAPDanbdq943TXNontw"
          alt=""
        />
        <p>
           ubey saab ComputerEngineer and Otaku from Eritos
        </p>
      </div>
      <div className="sidebarItem">
        <span className="sidebarTitle">CATEGORIES</span>
        <ul className="sidebarList">
          {cats.map((c) => (
            // / that's mean home page then our query gonan be cat then c.name
            <Link to={`/?cat=${c.name}`} className="link">
            <li className="sidebarListItem">{c.name}</li>
            </Link>
          ))}
        </ul>
      </div>
      <div className="sidebarItem">
        <span className="sidebarTitle">FOLLOW US</span>
        <div className="sidebarSocial">
          <i className="sidebarIcon fab fa-facebook-square"></i>
          <i className="sidebarIcon fab fa-twitter-square"></i>
          <i className="sidebarIcon fab fa-pinterest-square"></i>
          <i className="sidebarIcon fab fa-instagram-square"></i>
        </div>
      </div>
    </div>
  );
}
